
.search .search-box-wrapper {
  margin: 20px;
}
.search .shortcut-wrapper {
  position: fixed;
  top: 178px;
  bottom: 0;
  width: 100%;
}
.search .shortcut-wrapper .shortcut {
  height: 100%;
  overflow: hidden;
}
.search .shortcut-wrapper .shortcut .hotkey {
  margin: 0 20px 20px 20px;
}
.search .shortcut-wrapper .shortcut .hotkey .title {
  margin-top: 10px;
  margin-bottom: 20px;
  font-size: 18px;
  color: #ffcd32;
  font-style: italic;
}
.search .shortcut-wrapper .shortcut .hotkey .item {
  display: inline-block;
  padding: 5px 10px;
  margin: 0 20px 10px 0;
  border-radius: 6px;
  background: #333;
  font-size: 14px;
  color: #fff;
}
.search .search-result {
  position: fixed;
  width: 100%;
  top: 178px;
  bottom: 0;
}


